/*	################################################################
	1. GENERAL STRUCTURES
################################################################# */

/* Import fonts */
@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700,900);

* {
	margin: 0;
	padding: 0px;
    font-family: 'Lato', sans-serif;
}
body {
	background: #fff;
	margin: 0;
	color: #5a5a5a;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Lato', sans-serif;
	font-weight: 300;
	text-transform: capitalize;
}


p {
	padding: 0;
	margin-bottom: 12px;
    font-family: 'Lato', sans-serif;
	font-weight: 300;
	font-size: 18px;
	line-height: 28px;
	color: #666;
	margin-top: 10px;
}

html,
body {
	height: 100%;
}

.alignleft { float: left; }
.alignright { float: right; }
.aligncenter {
	margin-left: auto;
	margin-right: auto;
	display: block;
	clear: both;
}
.centered {text-align: center}
.mt {margin-top: 50px;}
.mb {margin-bottom: 50px;}
.mtb {margin-top: 50px; margin-bottom: 50px;}
.mtb2 { margin-top: 100px; margin-bottom: 100px;}
.ptb {padding-top: 80px; padding-bottom: 80px;}

.clear {
	clear: both;
	display: block;
	font-size: 0;
	height: 0;
	line-height: 0;
	width:100%;
}
::-moz-selection  {
	color: #fff;
	text-shadow:none;
	background:#2B2E31;
}
::selection {
	color: #fff;
	text-shadow:none;
	background:#2B2E31;
}
*,
*:after,
*:before {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	padding: 0;
	margin: 0;
}
a {
	padding: 0;
	margin: 0;
	text-decoration: none;
	-webkit-transition: background-color .4s linear, color .4s linear;
	-moz-transition: background-color .4s linear, color .4s linear;
	-o-transition: background-color .4s linear, color .4s linear;
	-ms-transition: background-color .4s linear, color .4s linear;
	transition: background-color .4s linear, color .4s linear;
	color: #1abc9c;
}
a:hover,
a:focus {
  text-decoration: none;
  color:#696E74;
}

/* BUTTONS */
.btn-green {
	background: #1abc9c;
	color: white;
	border-radius: 2px;
}

.btn-green:hover {
	background: #16a085;
	color: white;
}

/* HORIZONTAL LINE */
hr {
	border: 2px solid #fac102;
    width: 120px;
    border-radius: 10px;
}

/* H4 Global Modification */

h4 {
	font-weight: 700;
}

/* Color Theme */
.green {
	color: #1abc9c;
}

.orange {
	color: #fac102;
}

/* Size Helper */

.big {
	font-size: 60px;
}

.text16 {
	font-size: 16px;
}

.text28 {
	font-size: 28px;
}

.text28 i {
	padding: 8px;
}

/* HEADER SECTION */
#h {
	background: url(../images/dataglen_team1.JPG) no-repeat center top;
	padding-top: 60px;
	text-align:center;
	background-attachment: relative;
	background-position: center center;
	min-height: 700px;
	width: 100%;
	opacity: 0.9;

    -webkit-background-size: 100%;
    -moz-background-size: 100%;
    -o-background-size: 100%;
    background-size: 100%;

    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
}

#h h3.logo {
	font-weight: 900;
	text-align: center;
	color: white;
	margin-bottom: 120px;
	border: 3px solid white;
	width: 200px;
	padding: 12px;
}

#h h1 {
	color: whitesmoke;
	font-weight: 500;
	font-size: 70px;
}


#h .continue {
	font-size: 30px;
	color: #1abc9c;
	margin-top: 80px;
}

/* PRICING */
#ws .price {
	text-align: center;
	padding-top: 40px;
	padding-bottom: 40px;
	border: 3px solid #929292;
	margin-bottom: 20px;
}

#ws .price h1 {
	font-weight: 700;
	font-size: 70px;
}

#ws .price h6 {
	font-size: 16px;
	font-weight: 300;
}

#ws .price p {
	font-size: 22px;
}

#ws .price .btn-trans {
	color: #444;
	background-color: transparent;
	border-color: #444;
	border-width: 2px;
	font-weight: 700;
	font-size: 20px;
}

#ws .price .btn-trans:hover {
	background: #16a085
}

/* CALL TO ACTION */
#cta {
	background: #2f2f2f;
	color: white;
	padding-top: 50px;
	padding-bottom: 50px;
}

#cta h2 {
	text-align: center;
}

/* CALL TO ACTION */
#customers {
	background: #2f2f2f;
	color: white;
	padding-top: 50px;
	padding-bottom: 50px;
}

#customers h2 {
	text-align: center;
}

/* GREY SECTION */

#g {
	padding-top: 50px;
	padding-bottom: 50px;
	background: #f7f8fa;
}


#testimonial {
	padding-bottom: 50px;
	padding-top: 50px;
	min-height: 450px;
	background: #2f2f2f;
	color: white;
}

#testimonial p {
	font-size: 16px;
	color: #ccc;
}

#testimonial hr {
	border: solid 1px #ccc;
	width: 80px;
}


/* GENERAL CONF */

i {
	margin-right: 10px;
}

input {
	font-size: 18px;
	min-height: 40px;
	border-radius: 2px;
	line-height: 20px;
	padding: 11px 30px 12px;
	border: none;
	margin-bottom: 10px;
	background-color: #e9f0f2;
	-webkit-transition: background-color 0.2s;
	transition: background-color 0.2s;
}

.subscribe-input {
	float: left;
	width: 70%;
	text-align: left;
	margin-right: 2px;
}

.subscribe-submit {
	right: 0;
}

/*Overflow Hidden*/

.of {
	overflow: hidden;
}

.logo_margin {
	margin-top: 20vh;
}
